export default {
	data() {
		return {
			 nickname: '',
			 idcard: '',
		     address: ''
		}
	},
	methods: {
			foreign(){
				this.$api.register_applies_status({
					token: uni.getStorageSync("token"),
					user_id: uni.getStorageSync("userId")
				}).then(res=>{
					if(res.data.code === '1023'){
						uni.navigateTo({
							url:'../../pagesMy/ForeignCard/ForeignCard'
						})
					}
					if(res.data.code === '1099'){
						uni.showToast({
						    title: res.data.msg,
						    duration: 2000,
							icon:'none'
						});
					}
					if(res.data.code === '1022'){
						uni.setStorageSync("setInfo",1)
						uni.showToast({
						    title: res.data.msg,
						    duration: 2000,
							icon:'none'
						});
						setTimeout(function() {
							uni.navigateBack({
								delta:1
							})
						}, 1000);
						
					}
					
					if(res.data.code === '1021'||res.data.code === '1019'){
						uni.navigateTo({
							url:'../../pagesMy/ForeignCardDetail/ForeignCardDetail'
						})
					}
				})
				
				
			},
			// 选择城市
			pickercity(e){
				this.address = e.detail.value[0].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔/g, '') + ' ' +e.detail.value[1].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔|城区/g, '')
			},
		    next () {
		      if (!this.nickname) {
				  uni.showToast({
				      title: '请输入姓名',
				      duration: 1000,
					  icon:"none"
				  });
		       
		      } else if (!this.idcard) {
				  uni.showToast({
				      title: '请输入身份证号',
				      duration: 1000,
				  	  icon:"none"
				  });
		   
		      } else if (!this.address) {
				  uni.showToast({
				      title: '请输入详细地址',
				      duration: 1000,
				  	  icon:"none"
				  });
		      } else {
				  uni.showLoading({
				      title: '提交中...'
				  });
		    
		        this.$api.UserAddUserInfo({
		          token: uni.getStorageSync('token'),
		          user_id: uni.getStorageSync('userId'),
		          nickname: this.nickname, // 昵称
		          idcard: this.idcard, // 身份证号
		          politics: '其他', // 政治面貌
		          country: '中国', // 国家
		          avatar: 'https://web.detion.com/static/img/logo_icon.png',
		          address: this.address, // 地址
		          education: '其他' // 文化程度
		        }).then(res => {
		          uni.hideLoading();
		          if (res.data.code === '200') { // 完善提交后跳转资源
		            
					uni.showToast({
						title:res.data.msg,
						icon:"none"
					})
					uni.setStorageSync("setInfo",1)
				    uni.switchTab({
				        url: '/pages/index/index'
				    });
		            
		          } else if (res.data.code === '1010') {
					uni.clearStorageSync()
		            uni.reLaunch({
		                url: '../login/login'
		            });
					uni.showToast({
						title:'登录过期，请重新登录',
						icon:"none"
					})
		            
		          } else {
		            uni.showToast({
		            	title:res.data.msg,
		            	icon:"none"
		            })
		          }
		        })
		      }
		    },
		    login () {
				// 清除缓存并且跳到首页
		        uni.clearStorageSync()
				uni.reLaunch({
					url: '../login/login'
				});
		    }
		
	}
}
